.btn {
  display: inline-block;
  min-width: 94px;
  border-radius: 6px;
  color: #ffffff;
  text-align: center;
  padding: 11px 14px;
  transition: all 0.3s ease;
  border: 1px solid;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.05);
}

.btn:hover {
  transition: all 0.3s ease;
}

.btn.primary-btn {
  background-color: #1E313B;
  border-color: #1E313B;
}

.btn.primary-btn:hover {
  background-color: transparent;
  color: #1E313B;
}

