  background-color: transparent;
  color: #1E313B;
}

.btn.primary-btn.outline-btn:hover {
  background-color: #1E313B;
  color: #1E313B;
}

.btn.light-btn {
  background-color: #ffffff;
  border-color: #ffffff;
  color: #1E313B;
  padding: 10px 14px;
}

.btn.light-btn:hover {
  background-color: transparent;
  color: #ffffff;
}

.btn.purple-btn {
  background-color: #794EFF;
  border-color: #794EFF;
}

.btn.purple-btn:hover {
  background-color: transparent;
  color: #794EFF;
}

.card {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 10px;
  background-color: #ffffff;
  border-radius: 10px;
  cursor: pointer;
}

.card__img {
  margin-bottom: 44px;
}

.card__img img {
  display: block;
  max-width: 100%;
}

.card__title {
  font-size: 24px;
  line-height: 34px;
  margin-bottom: 10px;
}

.card__text {
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 14px;
}

.card__box {
  display: flex;
  gap: 16px;
  margin-top: auto;
}

.card__people {
  width: 48px;
  height: 48px;
}

.card__people img {
  display: block;
  width: 100%;
}

.card__inner {
  display: flex;
  gap: 22px;
  font-size: 14px;
  line-height: 22px;
}

.card__name {
  margin-bottom: 2px;
}

.card__read {
  margin-top: auto;
}

.card__read:before {
  display: inline-block;
  content: "";
  height: 2px;
  width: 2px;
  border-radius: 50%;
  color: red;
}

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  width: 100%;
}

@media (min-width: 768px) {
  .slick-slide img {
    display: block;
  }
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.slick-arrow {
  color: #1E313B;
  font-size: 0;
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 33.33%;
}

.slick-arrow:before {
  font-size: 16px;
  font-family: 'icomoon';
}

.slick-prev {
  z-index: 1;
}

.slick-prev:before {
  content: '\e907';
}

.slick-next:before {
  content: '\e900';
}

